@import '../../assets/scss/com.scss';
.details{
    width: 100%;
    height: 100%;
    background-color: $pageBackColor;
    .details_fix{
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 110px;
        background: #FFFFFF;
        border-top: 1px solid #B7B7B7;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        font-family: Microsoft YaHei;
        font-weight: 400;
        color: #9E9E9E;
        .fix_box{
            width:160px;
            display:flex;
            flex-direction: column;
            align-items: center;
            img{
                margin-bottom: 12px;
                width:42px;
                height:36px
            }   
        }
    }
    .details_name{
        margin-top: 20px;
        width: 100%;
        height: 88px;
        background: #FFFFFF;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 30px;
        box-sizing: border-box;
        .name_left{
            display: flex;
            align-items: center;
            .left_one{
                font-size: 28px;
                font-family: Microsoft YaHei;
                font-weight: 400;
                color: #363636;
            }
            .left_two{
                width: 350px;
                font-size: 24px;
                font-family: Microsoft YaHei;
                font-weight: 400;
                color: #B7B7B7;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-left: 28px;
                box-sizing: border-box;
                img{
                    width: 7px;
                    height: 14px;
                    margin-left: 19px;
                }
            }
        }
        .name_right{
            font-size: 26px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #7E7E7E;
        }
    }
    .details_box{
        width: 100%;
        height: 390px;
        background-color: #fff;
        padding: 154px 0 0 0;
        margin-top: 20px;
        overflow: hidden;
        .conte{
            height: 100%;
            display: flex;
            .box_line{
                height: 18px;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                position: relative;
                .line_image{
                    position: absolute;
                    top: -40px;
                    left: 0;
                    width: 100%;
                    height: 20px;
                    display:flex;
                    align-items: center;
                }
                .line_self{
                    position: absolute;
                    top: -80px;
                    right: -5px;
                    img{
                        width: 30px;
                        height: 36px;
                    }

                }
                .line_name{
                    position: absolute;
                    right: -5px;
                    bottom: -20px;
                    width: 30px;
                    height: 10px;
                    font-size: 30px;
                    font-family: Microsoft YaHei;
                    font-weight: 400;
                    color: #363636;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
                .colo{
                    font-weight: bold;
                    color: $myBackColor !important;
                    img{
                        width: 30px;
                        height: 68px;
                    }
                }
                img{
                    width: 12px;
                    height: 14px;
                }
            }
        }
    }
    .details_banner{
        // banner样式
        width: 100%;
        height: 198px;
        .banner_main{
            width: 100%;
            height: 100%;
            padding: 30px 85px;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            .main_state{
                // 车辆运行中样式
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                .state_top{
                    // width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    img{
                        width: 18px;
                        height: 18px;
                        margin-left: 66px;
                    }
                }
                .state_i{
                    font-size: 36px;
                    font-family: Microsoft YaHei;
                    font-weight: bold;
                    color: $myBackColor;
                }
                .state_mid{
                    font-size: 48px;
                    font-family: Microsoft YaHei;
                    font-weight: bold;
                    color: $myBackColor;
                    span{
                        font-size: 24px;
                    }
                }
                .state_fl{
                    font-size: 30px;
                    font-family: Microsoft YaHei;
                    font-weight: 400;
                    color: #7E7E7E;
                }

            }
            .main_mero{
                // 查看更多样式
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                img{
                    width: 47px;
                    height: 47px;
                    margin-bottom: 15px;
                }
                span{
                    font-size: 30px;
                    font-family: Microsoft YaHei;
                    font-weight: 400;
                    color: #7E7E7E;
                }
            }
            .main_received{
                // 已收班样式
                display: flex;
                flex-direction: column;
                align-items: center;
                .received_text{
                    font-size: 36px;
                    font-family: Microsoft YaHei;
                    font-weight: bold;
                    color: #7E7E7E;
                    padding-bottom: 20px;
                    box-sizing: border-box;
                }
                .received_font{
                    font-size: 30px;
                    font-family: Microsoft YaHei;
                    font-weight: 400;
                    color: #7E7E7E;
                }
            }
            .main_notyet{
                // 未到站样式
                display: flex;
                flex-direction: column;
                align-items: center;
                .notyet_text{
                    font-size: 36px;
                    font-family: Microsoft YaHei;
                    font-weight: bold;
                    color: $myBackColor;
                    padding-bottom: 20px;
                    box-sizing: border-box;
                }
                .notyet_font{
                    font-size: 30px;
                    font-family: Microsoft YaHei;
                    font-weight: 400;
                    color: #7E7E7E;
                }
            }
        }
    }
    .details_top{
        width: 100%;
        height: 100px;
        background-color: $myBackColor;
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-sizing: border-box;
        overflow: hidden;
        .header_left{
            display: flex;
            align-items: center;
            .top_left{
                font-size: 36px;
                font-family: Microsoft YaHei;
                font-weight: bold;
                color: #FFFFFF;
            }
            .top_one{
                height: 100px;
                margin-right: 70px;
                position: relative;
            }
        }
        img{
            width: 50px;
            height: 48px;
            margin-right: 30px;
        }
    }
}