.header{
    width: 100%;
    height: 129px;
    padding: 0 30px;
    box-sizing: border-box;
    background-color: #fff;
    .header_box{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        border-bottom: 2px solid #B7B7B7;
        font-size: 30px;
        font-family: Microsoft YaHei;
        font-weight: 400;
        color: #7E7E7E;
        padding: 24px 0;
        overflow: auto;
        box-sizing: border-box;
        .box_left{
            white-space:nowrap;
            .left_text{
                color: #363636;
            }
        }
        .box_right{
            white-space:nowrap;
            height: 100%;
            display: flex;
            align-items: flex-end;
        }
    }
}